.main-container {
    margin: 0;
    padding: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    .app-container-1 {
        justify-content: center;
        align-items: flex-end;
        margin: 25px;
        padding: 50px;
        width: auto;
        width: 70%;
    }
    .app-container-2 {
        justify-content: center;
        align-items: flex-end;
        margin: 25px;
        padding: 50px;
        width: auto;
        width: 30%;
        .submit-btn {
            text-align: center;
    border-radius: 10px;
    font-size: 1em;
    color: white;
    background: rgb(44, 209, 88);
    background: -moz-linear-gradient(233deg, rgba(44, 209, 88, 1) 0%, rgba(44, 108, 224, 1) 100%);
    background: -webkit-linear-gradient(233deg, rgba(44, 209, 88, 1) 0%, rgba(44, 108, 224, 1) 100%);
    background: linear-gradient(233deg, rgba(44, 209, 88, 1) 0%, rgba(44, 108, 224, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#2cd158", endColorstr="#2c6ce0", GradientType=1);
    padding: 5px 15px;
    font-weight: 500;
            &:hover {
                background-color: #2980b9;
            }
        }
    }
}
.title {
    margin: 25px;
    text-align: center;
    color: #2c4658;
}